<template>
  <div>
    <div>
      <div>
        <el-button icon="el-icon-plus" size="small" style="margin-top: 10px;background-color: #009966;color: #ffffff;"
                   @click="showAddCarriersView">

        </el-button>
        <el-input
          placeholder="..."
          prefix-icon="el-icon-search"
          style="width: 300px"
          clearable
          @change="initCarriers"
          @clear="initCarriers"
          @keyup.enter.native="initCarriers"
          v-model="keyword">
        </el-input>
      </div>
      <el-dialog
        :title="dialogtitle"
        :visible.sync="dialogVisible"
        width="60%">
        <div>
          <table class="addTaskTable">
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.sendcompany"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.sendaddress"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.sendlinkman"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.sendphone"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.receivecompany"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.fkReceiveaddress"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.receivelinkman"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.receivephone"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-date-picker
                  v-model="carriers.leaverdate"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </td>
              <td></td>
              <td>
                <el-date-picker
                  v-model="carriers.receivedate"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-select v-model="carriers.finishedstate" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.insurancecost"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.transportcost"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.othercost"></el-input>
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <el-input v-model="carriers.totalcost"></el-input>
              </td>
              <td></td>
              <td>
                <el-input v-model="carriers.remark"></el-input>
              </td>
            </tr>
          </table>
        </div>
        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false" type="info"> </el-button>
    <el-button type="primary" @click="addCarriers" style="background-color: #009966;color: #ffffff"> </el-button>
  </span>
      </el-dialog>
      <div style="margin-top: 10px">
        <el-table
          :data="carriersS"
          stripe
          border
          v-loading="loading"
          style="width: 100%">
          <el-table-column
            prop="carriersid"
            label=""
            fixed
            width="100">
          </el-table-column>
          <el-table-column
            prop="sendlinkman"
            fixed
            label="">
          </el-table-column>
          <el-table-column
            prop="sendcompany"
            label=""
            width="100">
          </el-table-column>
          <el-table-column
            prop="sendaddress"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="sendphone"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivecompany"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="fkReceiveaddress"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivelinkman"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivephone"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="leaverdate"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivedate"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.finishedstate==0" type="danger"></el-tag>
              <el-tag v-else-if="scope.row.finishedstate==1" type="info"></el-tag>
              <el-tag v-else-if="scope.row.finishedstate==2" type="warning"></el-tag>
              <el-tag v-else type="success"></el-tag>
            </template>
          </el-table-column>
          <el-table-column
            prop="insurancecost"
            label="">
          </el-table-column>
          <el-table-column
            prop="transportcost"
            label="">
          </el-table-column>
          <el-table-column
            prop="othercost"
            label="">
          </el-table-column>
          <el-table-column
            prop="totalcost"
            label="">
          </el-table-column>
          <el-table-column
            prop="remark"
            label="">
          </el-table-column>
          <el-table-column
            prop="checkintime"
            width="150"
            label="">
          </el-table-column>
          <el-table-column label="" width="200px" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="small"
                @click="handleEdit(scope.$index, scope.row)"icon="el-icon-edit" style="background-color: #009966;color: #ffffff;">
              </el-button>
              <el-button
                size="small"
                type="info"
                @click="handleDelete(scope.$index, scope.row)"icon="el-icon-delete">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div>
        <el-pagination
          @current-change="currentChange"
          @size-change="sizeChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :page-sizes="[5,10,15,20]"
          style="display: flex;justify-content: flex-end;margin-right: 10px"
          layout="sizes,prev,pager,next,jumper,->,total,slot"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ReceiveOrder",
    data() {
      return {
        dialogtitle:'',
        carriersS:[],
        total: 1,
        currentPage: 1,
        pageSize: 5,
        keyword: null,
        loading: true,
        dialogVisible: false,
        options: [
          {
            value: 0,
            label: ""
          },
          {
            value: 1,
            label: ""
          },
          {
            value: 2,
            label: ""
          }, {
            value: 3,
            label: ""
          }],
        carriers: {
          carriersid: '',
          sendcompany: '',
          sendaddress: '',
          sendlinkman: '',
          sendphone: '',
          receivecompany: '',
          fkReceiveaddress: '',
          receivelinkman: '',
          receivephone: '',
          leaverdate: '',
          receivedate: '',
          finishedstate: 0,
          insurancecost: '',
          transportcost: '',
          othercost: '',
          totalcost: '',
          remark: ''
        }
      }
    },
    mounted() {
     this.initCarriers();
    },
    methods: {
      handleDelete(index,row) {
        this.$confirm(''+row.carriersid+'?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteRequest("/task/"+row.carriersid).then(resp=>{
            if (resp) {
              this.initCarriers();
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });


      },
      handleEdit(index,row) {
        this.carriers = row;
        this.dialogVisible = true;
        this.dialogtitle = "";

      },
      sizeChange(val) {
        this.pageSize = val;
        this.initCarriers();
      },
      currentChange(val) {
        this.currentPage = val;
        this.initCarriers();
      },
     initCarriers() {
        let url = "/task/?page=" + this.currentPage + "&size=" + this.pageSize;
        if (this.keyword) {
          url += "&keyword=" + this.keyword;
        }
        this.getRequest(url).then(resp => {
          if (resp) {
            this.carriersS = resp.data;
            this.total = resp.total;
            this.loading = false;
          }
        });
      },
      addCarriers() {
        if (this.carriers.carriersid) {
          //
          this.putRequest("/task/", this.carriers).then(resp => {
            if (resp) {
              this.dialogVisible = false;
              this.initCarriers();
            }
          });
        } else {
        this.postRequest("/task/", this.carriers).then(resp => {
          if (resp) {
            this.dialogVisible = false;
            this.initCarriers();
          }
        });
        }
      },
      showAddCarriersView() {
        this.carriers= {
          carriersid: '',
            sendcompany: '',
            sendaddress: '',
            sendlinkman: '',
            sendphone: '',
            receivecompany: '',
            fkReceiveaddress: '',
            receivelinkman: '',
            receivephone: '',
            leaverdate: '',
            receivedate: '',
            finishedstate: 0,
            insurancecost: '',
            transportcost: '',
            othercost: '',
            totalcost: '',
            remark: ''
        }
        this.dialogVisible = true;
        this.dialogtitle = "";
      },
    }
  }
</script>

<style scoped>

</style>



