<template>
    <div>
    <div></div>
    <div style="margin-top: 10px">
        <el-table
          :data="carriers"
          stripe
          border
          v-loading="loading"
          style="width: 100%">
          <el-table-column
            prop="carriersid"
            label=""
            fixed
            width="100">
          </el-table-column>
          <el-table-column
            prop="sendlinkman"
            fixed
            label="">
          </el-table-column>
          <el-table-column
            prop="sendcompany"
            label=""
            width="100">
          </el-table-column>
          <el-table-column
            prop="sendaddress"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="sendphone"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivecompany"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="fkReceiveaddress"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivelinkman"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivephone"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="leaverdate"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivedate"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.finishedstate==0" type="danger"></el-tag>
              <el-tag v-else-if="scope.row.finishedstate==1" type="danger"></el-tag>
              <el-tag v-else-if="scope.row.finishedstate==2" type="warning"></el-tag>
              <el-tag v-else type="success"></el-tag>
            </template>
          </el-table-column>
          <el-table-column
            prop="insurancecost"
            label="">
          </el-table-column>
          <el-table-column
            prop="transportcost"
            label="">
          </el-table-column>
          <el-table-column
            prop="othercost"
            label="">
          </el-table-column>
          <el-table-column
            prop="totalcost"
            label="">
          </el-table-column>
          <el-table-column
            prop="remark"
            label="">
          </el-table-column>
          <el-table-column
            prop="checkintime"
            width="150"
            label="">
          </el-table-column>
          <el-table-column label="" width="150" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="receipted(scope.row)">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
    export default {
        name: "WriteOrder",
      data() {
          return {
            carriers:[],
            loading: true
          }
      },
      mounted() {
        this.initCarriers();
      },
      methods: {
          //
        receipted(row) {
          console.log(this.carriers)
          this.putRequest("/task/rece/?carriersid="+row.carriersid).then(resp=>{
            if (resp) {
              this.initCarriers();

            }
          })

        },
        initCarriers() {
          this.getRequest("/task/rece/").then(resp=>{
            if (resp) {
              this.carriers = resp.data;
              this.loading = false;
            }
          })
        },
      }
    }
</script>

<style scoped>

</style>



