<template>
  <div style="margin-top: 10px">
    <el-tag type="primary"></el-tag>
    <div>
      <el-table
        :data="carriersS"
        stripe
        border
        style="width: 100%">
        <el-table-column
          prop="carriersid"
          label=""
          fixed
          width="100">
        </el-table-column>
        <el-table-column
          prop="sendlinkman"
          fixed
          label="">
        </el-table-column>
        <el-table-column
          prop="sendcompany"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="sendaddress"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="sendphone"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="receivecompany"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="fkReceiveaddress"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="receivelinkman"
          label="">
        </el-table-column>
        <el-table-column
          prop="receivephone"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="leaverdate"
          width="150"
          label="">
        </el-table-column>
        <el-table-column
          prop="receivedate"
          width="150"
          value-format="yyyy-MM-dd"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.finishedstate==0" type="danger"></el-tag>
            <el-tag v-else-if="scope.row.finishedstate==1" type="info"></el-tag>
            <el-tag v-else-if="scope.row.finishedstate==2" type="warning"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="insurancecost"
          label="">
        </el-table-column>
        <el-table-column
          prop="transportcost"
          label="">
        </el-table-column>
        <el-table-column
          prop="othercost"
          label="">
        </el-table-column>
        <el-table-column
          prop="totalcost"
          label="">
        </el-table-column>
        <el-table-column
          prop="remark"
          label="">
        </el-table-column>
        <el-table-column
          prop="checkintime"
          width="150"
          label="">
        </el-table-column>
        <el-table-column label="" width="150" fixed="right">
          <template slot-scope="scope">
            <!--<el-dialog
              title=""
              :visible.sync="dialogVisible2"
              modal-append-to-body="false"
              append-to-body="false"
              width="40%">
              <table>
                <el-table
                  :data="carriersS"
                  stripe
                  border
                  style="width: 100%">
                  <el-table-column
                    prop="carriersid"
                    label=""
                    fixed
                    width="100">
                  </el-table-column>
                  <el-table-column
                    prop="insurancecost"
                    label="">
                  </el-table-column>
                  <el-table-column
                    prop="transportcost"
                    label="">
                  </el-table-column>
                  <el-table-column
                    prop="othercost"
                    label="">
                  </el-table-column>
                  <el-table-column
                    prop="totalcost"
                    label="">
                  </el-table-column>
                </el-table>
              </table>
              <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="count"> </el-button>
          </span>
            </el-dialog>-->
            <el-button type="primary" size="small" @click="corr(scope.$index, scope.row)"></el-button>
            <el-button type="success" size="small" @click="modifyCost1(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="display: flex;justify-content: flex-end">
      <el-pagination
        background
        :page-sizes="[5,10,15,20]"
        :page-size="pageSize1"
        :current-page="currentPage1"
        @current-change="currentChange1"
        @size-change="sizeChange1"
        style="margin-right: 10px"
        layout="sizes, prev, pager, next, jumper, ->, total, slot"
        :total="total1">
      </el-pagination>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="60%">
      <div>
        <table class="addTaskTable">
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.sendcompany"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.sendaddress"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.sendlinkman"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.sendphone"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.receivecompany"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.fkReceiveaddress"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.receivelinkman"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.receivephone"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-date-picker
                v-model="carriers.leaverdate"
                type="date"
                placeholder="">
              </el-date-picker>
            </td>
            <td></td>
            <td>
              <el-date-picker
                v-model="carriers.receivedate"
                type="date"
                placeholder="">
              </el-date-picker>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-select v-model="carriers.finishedstate" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.insurancecost"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.transportcost"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.othercost"></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="carriers.totalcost"></el-input>
            </td>
            <td></td>
            <td>
              <el-input v-model="carriers.remark"></el-input>
            </td>
          </tr>
        </table>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="handleDelete"> </el-button>
  </span>
    </el-dialog>
    <div style="margin-top: 5px">
      <el-tag type="primary"></el-tag>
      <div>
        <el-table
          :data="sches"
          stripe
          border
          style="width: 100%">
          <el-table-column
            prop="schedulingid"
            label=""
            fixed
            width="100">
          </el-table-column>
          <el-table-column
            prop="starttime"
            width="180"
            label="">
          </el-table-column>
          <el-table-column
            prop="fkCarriersid"
            label=""
            width="100">
          </el-table-column>
          <el-table-column
            prop="fkTruckid"
            label=""
            width="100">
          </el-table-column>
          <el-table-column
            prop="oilcost"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="toll"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="fine"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="othercost"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="totalcost"
            label="">
          </el-table-column>
          <el-table-column
            prop="remark"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="receivedate"
            width="150"
            label="">
          </el-table-column>
          <el-table-column label="" width="150" fixed="right">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="count1(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div style="display: flex;justify-content: flex-end">
        <el-pagination
          background
          :page-sizes="[5,10,15,20]"
          :page-size="pageSize2"
          :current-page="currentPage2"
          @current-change="currentChange2"
          @size-change="sizeChange2"
          style="margin-right: 10px"
          layout="sizes, prev, pager, next, jumper, ->, total, slot"
          :total="total2">
        </el-pagination>
      </div>
      <div>
        <el-dialog
          title=""
          :visible.sync="dialogVisible3"
          width="40%">
          <span></span>
          <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="count2"> </el-button>
  </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Coststatistic",
    data() {
      return {
        dialogVisible3:false,
        pageSize2:5,
        currentPage2:1,
        total2:0,
        sches:[],
        carriersS: [],
        carriersid: '',
        totalcosts: [],
        pageSize1: 5,
        currentPage1: 1,
        total1: 0,
        dialogVisible:false,
        dialogVisible2:false,
        carrier: [],
        options: [
          {
            value: 0,
            label: ""
          },
          {
            value: 1,
            label: ""
          },
          {
            value: 2,
            label: ""
          }, {
            value: 3,
            label: ""
          }],
        carriers: {
          carriersid: '',
          sendcompany: '',
          sendaddress: '',
          sendlinkman: '',
          sendphone: '',
          receivecompany: '',
          fkReceiveaddress: '',
          receivelinkman: '',
          receivephone: '',
          leaverdate: '',
          receivedate: '',
          finishedstate: 0,
          insurancecost: '',
          transportcost: '',
          othercost: '',
          totalcost: '',
          remark: ''
        }



      }
    },
    mounted() {
      this.initCarriers();
      this.initCarrier();
      this.initSches();

    },
    methods: {
      count2(){
        this.dialogVisible3=false;
      },
      count1(index,row){
        //
     this.dialogVisible3=true;

      },
      sizeChange2(val){
        this.pageSize2=val;
        this.initSches();
      },
      currentChange2(val){
        this.currentPage2=val;
        this.initSches();
      },
      initSches(){
        this.getRequest("/cost/sche/?page=" + this.currentPage2 + "&size=" + this.pageSize2).then(resp=>{
          if(resp){
            this.sches=resp.data;
            this.total2=resp.total;
          }
        })
      },
      corr(index,row){
        //,id
        //12
        if(row.finishedstate==2){
          this.dialogVisible2=true;
          //key-value
          this.postKeyValueRequest("/cost/count/",{carriersid:row.carriersid,totalcost:row.totalcost}).then(resp=>{
            if(resp){
              this.initCarriers();
              //
            }
          })
        }else{
          this.$confirm(''+row.carriersid+'', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }

      },

      initCarrier() {
        this.getRequest("/cost/carriers").then(resp => {
          if (resp) {
            this.totalcosts = resp;
          }
        })
      },
      handleDelete() {
        this.putRequest("/cost/", this.carriers).then(resp => {
          if (resp) {
            this.dialogVisible = false;
            this.initCarriers();
          }
        })
      },
      modifyCost1(index, row) {
        //
        this.dialogVisible = true;
        this.carriers = row;
      },
      sizeChange1(val) {
        this.pageSize1 = val;
        this.initCarriers();
      },
      currentChange1(val) {
        this.currentPage1 = val;
        this.initCarriers();
      },
      initCarriers() {
        this.getRequest("/cost/?page=" + this.currentPage1 + "&size=" + this.pageSize1).then(resp => {
          if (resp) {
            this.carriersS = resp.data;
            this.total1 = resp.total;
          }
        })
      },

    }
  }
</script>

<style scoped>

</style>



