<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="loginA"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      width="350px">
    <div slot="header" class="clearfix">
      <span></span>
    </div>
    <table>
      <tr>
        <td> </td>
        <td><el-input v-model="user.account" placeholder=""></el-input></td>
      </tr>
      <tr>
        <td></td>
        <td><el-input type="password" v-model="user.password" placeholder=""></el-input></td>
      </tr>
      <tr>
        <td colspan="2">
          <el-button type="primary" @keyup.native.enter="doLogin" @click="doLogin" style="width: 300px"v-loading="loading"></el-button>
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <el-button type="info" @click="registerUser" style="width: 300px"></el-button>
        </td>
      </tr>
    </table>

    </el-dialog>

  <div>
    <el-dialog
      title=""
      :visible.sync="registerdialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"

      width="50%">
      <div>
        <el-form :model="user" status-icon :rules="rules" ref="user" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="username">
            <el-input v-model="user.username" autocomplete="on"></el-input>
          </el-form-item>
          <el-form-item label="" prop="username">
            <el-radio-group v-model="user.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="account">
            <el-input v-model="user.account" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="user.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkpass">
            <el-input type="password" v-model="user.checkpass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model.number="user.phone" autocomplete="on"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input type="email" v-model="user.email" autocomplete="on"></el-input>
          </el-form-item>
          <el-form-item label="" prop="fkRoleid">
            <el-select v-model="user.fkRoleid" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addUser('user')"></el-button>
            <el-button @click="resetForm('user')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
  </div>
</template>

<script>
    export default {
        name: "ModelLogin",
      data () {
        var validateName = (rule, value, callback) => {
          if (!value) {
            callback(new Error(''));
          }
          callback()
        };

        var validateAccount = (rule, value, callback) => {
          if (!value) {
            callback(new Error(''));
          } else {
            this.getRequest("/account?account="+value).then(resp=>{
              if (resp == 1) {
                callback(new error(""));
              } else {
                callback();
              }
            })
          }
          //callback();
        };
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''));
          } else {
            if (this.user.checkpass !== '') {
              this.$refs.user.validateField('checkpass');
            }
            callback();
          }
        };
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''));
          } else if (value !== this.user.password) {
            callback(new Error('!'));
          } else {
            callback();
          }
        };
        var checkPhone = (rule, value, callback) => {
          var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
          if (!value) {
            return callback(new Error(''))
          }
          setTimeout(() => {
            // Number.isIntegeres6,
            // +

            if (!Number.isInteger(+value)) {
              callback(new Error(''))
            } else {
              if (phoneReg.test(value)) {
                callback()
              } else {
                callback(new Error(''))
              }
            }
          }, 100)
        };
        var checkEmail = (rule, value, callback) => {
          var mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
          if (!value) {
            return callback(new Error(''))
          }
          setTimeout(() => {
            if (mailReg.test(value)) {
              callback()
            } else {
              callback(new Error(''))
            }
          }, 100)
        };
        var validateRole = (rule, value, callback) => {

          if (!value) {
            callback(new Error(''));
          }
          callback()
        };
        return {
          registerdialog:false,
          loading:false,
          loginA:true,
          user: {
            username:'',
            sex:'',
            account:'',
            password:'',
            checkpass:'',
            phone:'',
            email:'',
            fkRoleid:'',
            checktime:'',
            isdelete:'',
            altertime:''
          },
          rules: {
            username: [
              { validator: validateName, trigger: 'blur' }
            ],
            account: [
              { validator: validateAccount, trigger: 'blur' }
            ],
            password:[
              { validator: validatePass, trigger: 'blur' }
            ],
            checkpass: [
              { validator: validatePass2, trigger: 'blur' }
            ],
            phone:[
              { validator: checkPhone, trigger: 'blur' }
            ],
            fk_roleid:[
              { required: true, validator: validateRole, trigger: 'change' }
            ],
            email:[
              { validator: checkEmail, trigger: 'blur'}
            ]
          },
          options: [{
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          }, {
            value: '4',
            label: ''
          }, {
            value: '5',
            label: ''
          }],
          value: ''
        }
      },
      created(){

        this.keyupSubmit()
      },
      methods: {
        keyupSubmit(){
          document.onkeydown=e=>{
            let _key=window.event.keyCode;
            if(_key===13){
              this.doLogin()
            }
          }
        },
        doLogin () {
          this.loading=true;
          this.postRequest("/doLogin",this.user).then(data=>{
            if (data){
              window.sessionStorage.setItem("user",JSON.stringify(data.obj));
              this.loading=false;
              this.loginA=false;
              this.$router.replace("/home");
              window.location.reload();
            }
            this.loading=false;
          })
        },
        registerUser(){
          this.registerdialog=true
        },
        addUser(formName){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.postRequest("/register",this.user).then(data=>{
                this.registerdialog=false

              })
            } else {
              alert('!!');
              return false;
            }
          });

        },
        resetForm(formName){
          this.$refs[formName].resetFields();
        }
      }
    }

</script>

<style scoped>

</style>



